<template>
  <view class="container">
    <!-- Âú∞ÂõæÁªÑ‰ª∂ - ‰ΩøÁî®Êù°‰ª∂ÁºñËØë -->
    <!-- #ifdef MP-WEIXIN -->
    <map 
      id="map"
      :latitude="latitude"
      :longitude="longitude"
      :scale="scale"
      :markers="markers"
      :polyline="polyline"
      :show-location="true"
      class="map-container"
    ></map>
    <!-- #endif -->
    
    <!-- #ifdef H5 -->
    <view class="map-container" id="map"></view>
    <!-- #endif -->
    
    <!-- È°∂ÈÉ®Ê†áÈ¢òÊ†è -->
    <view class="header">
      <view class="back-btn" @click="goBack">‚Üê</view>
      <text class="title">Âú∞ÂõæÂØºËà™</text>
    </view>
    
    <!-- ‰ΩçÁΩÆËæìÂÖ•Âå∫Âüü -->
    <view class="location-info">
      <view class="location-item">
        <view class="location-icon start-icon">A</view>
        <input 
          type="text" 
          class="location-input" 
          v-model="startPoint" 
          placeholder="ËØ∑ËæìÂÖ•Ëµ∑ÁÇπ‰ΩçÁΩÆ"
          @focus="onStartFocus"
        />
        <view class="locate-btn" @click="useCurrentLocation">üìç</view>
      </view>
      <view class="location-item">
        <view class="location-icon end-icon">B</view>
        <input 
          type="text" 
          class="location-input" 
          v-model="endPoint" 
          placeholder="ËØ∑ËæìÂÖ•ÁªàÁÇπ‰ΩçÁΩÆ"
          @focus="onEndFocus"
        />
        <view class="clear-btn" @click="clearEndPoint">√ó</view>
      </view>
    </view>
    
    <!-- Ë∑ØÁ∫øÁ±ªÂûãÈÄâÊã© -->
    <view class="route-type-selector">
      <view 
        class="route-type-btn" 
        :class="{ active: routeType === 'drive' }"
        @click="setRouteType('drive')"
      >
        <text class="btn-icon">üöó</text>
        <text class="btn-text">È©æËΩ¶</text>
      </view>
      <view 
        class="route-type-btn" 
        :class="{ active: routeType === 'bus' }"
        @click="setRouteType('bus')"
      >
        <text class="btn-icon">üöå</text>
        <text class="btn-text">ÂÖ¨‰∫§</text>
      </view>
      <view 
        class="route-type-btn" 
        :class="{ active: routeType === 'walk' }"
        @click="setRouteType('walk')"
      >
        <text class="btn-icon">üö∂</text>
        <text class="btn-text">Ê≠•Ë°å</text>
      </view>
    </view>
    
    <!-- ÂØºËà™ÊåâÈíÆ -->
    <view class="nav-buttons">
      <button class="nav-btn primary-btn" @click="openExternalNavigation" :disabled="!canNavigate">
        <text>ÊâìÂºÄÂØºËà™</text>
      </button>
      <button class="nav-btn secondary-btn" @click="planRoute" :disabled="!canNavigate || loading">
        <text>{{ loading ? 'ËßÑÂàí‰∏≠...' : 'Ë∑ØÁ∫øËßÑÂàí' }}</text>
      </button>
    </view>
    
    <!-- Ë∑ØÁ∫ø‰ø°ÊÅØ -->
    <view class="route-info" v-if="currentRoute">
      <view class="route-header">
        <text class="route-title">Ë∑ØÁ∫øËØ¶ÊÉÖ</text>
        <text class="route-summary">{{ currentRoute.distance }} ¬∑ {{ currentRoute.duration }}</text>
      </view>
      <scroll-view class="route-steps" scroll-y>
        <view 
          class="route-step" 
          v-for="(step, index) in currentRoute.steps" 
          :key="index"
        >
          <view class="step-icon">{{ step.icon }}</view>
          <view class="step-content">
            <text class="step-instruction">{{ step.instruction }}</text>
            <text class="step-distance">{{ step.distance }}</text>
          </view>
        </view>
      </scroll-view>
    </view>
    
    <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
    <view class="loading-mask" v-if="loading">
      <view class="loading">
        <view class="loading-spinner"></view>
        <text>Ê≠£Âú®ËßÑÂàíË∑ØÁ∫ø...</text>
      </view>
    </view>
  </view>
</template>

<script>
// È´òÂæ∑Âú∞ÂõæWebÊúçÂä°Key
const AMAP_WEB_KEY = 'becc8508eddef29e75d2b60ec9690cdd';

export default {
  name: 'UniversalNavigation',
  data() {
    return {
      // Âú∞ÂõæÁõ∏ÂÖ≥ - ÂæÆ‰ø°Â∞èÁ®ãÂ∫è‰ΩøÁî®
      latitude: 39.90923,
      longitude: 116.397428,
      scale: 13,
      markers: [],
      polyline: [],
      
      // H5Âú∞ÂõæÂÆû‰æã
      map: null,
      
      // ‰ΩçÁΩÆ‰ø°ÊÅØ
      startPoint: 'ÊàëÁöÑ‰ΩçÁΩÆ',
      endPoint: '',
      routeType: 'drive',
      
      // Ë∑ØÁ∫ø‰ø°ÊÅØ
      currentRoute: null,
      loading: false,
      
      // ÂΩìÂâç‰ΩçÁΩÆ
      currentLocation: null
    }
  },
  computed: {
    canNavigate() {
      return this.startPoint && this.endPoint;
    }
  },
  onLoad() {
    this.initMap();
  },
  methods: {
    // ÂàùÂßãÂåñÂú∞Âõæ - Â§öÂπ≥Âè∞ÂÖºÂÆπ
    initMap() {
      // #ifdef MP-WEIXIN
      this.getCurrentLocation();
      // #endif
      
      // #ifdef H5
      this.initH5Map();
      // #endif
    },
    
    // H5Âú∞ÂõæÂàùÂßãÂåñ
    initH5Map() {
      // Âä®ÊÄÅÂä†ËΩΩÈ´òÂæ∑Âú∞Âõæ
      if (typeof window !== 'undefined' && !window.AMap) {
        const script = document.createElement('script');
        script.src = `https://webapi.amap.com/maps?v=1.4.15&key=${AMAP_WEB_KEY}`;
        script.onload = () => {
          setTimeout(() => {
            this.createH5Map();
          }, 500);
        };
        document.head.appendChild(script);
      } else {
        this.createH5Map();
      }
    },
    
    // ÂàõÂª∫H5Âú∞Âõæ
    createH5Map() {
      try {
        this.map = new AMap.Map('map', {
          zoom: 13,
          center: [116.397428, 39.90923],
          viewMode: '2D'
        });
        
        // Ëé∑ÂèñÂΩìÂâç‰ΩçÁΩÆ
        this.getCurrentLocation();
      } catch (error) {
        console.error('H5Âú∞ÂõæÂàùÂßãÂåñÂ§±Ë¥•:', error);
      }
    },
    
    // Ëé∑ÂèñÂΩìÂâç‰ΩçÁΩÆ - Â§öÂπ≥Âè∞ÂÖºÂÆπ
    getCurrentLocation() {
      uni.getLocation({
        type: 'gcj02', // È´òÂæ∑ÂùêÊ†áÁ≥ª
        success: (res) => {
          this.latitude = res.latitude;
          this.longitude = res.longitude;
          this.currentLocation = {
            latitude: res.latitude,
            longitude: res.longitude
          };
          
          // #ifdef MP-WEIXIN
          this.addWeixinMarker(res.latitude, res.longitude, 'ÊàëÁöÑ‰ΩçÁΩÆ', 0);
          // #endif
          
          // #ifdef H5
          if (this.map) {
            this.map.setCenter([res.longitude, res.latitude]);
            this.map.setZoom(15);
            this.addH5Marker(res.longitude, res.latitude, 'ÊàëÁöÑ‰ΩçÁΩÆ');
          }
          // #endif
          
          // Ëé∑ÂèñÂú∞ÂùÄÂêçÁß∞
          this.getAddressName(res.longitude, res.latitude);
        },
        fail: (err) => {
          console.warn('Ëé∑Âèñ‰ΩçÁΩÆÂ§±Ë¥•:', err);
          uni.showToast({
            title: 'Ëé∑Âèñ‰ΩçÁΩÆÂ§±Ë¥•ÔºåËØ∑ÊâãÂä®ËæìÂÖ•Ëµ∑ÁÇπ',
            icon: 'none'
          });
        }
      });
    },
    
    // ÂæÆ‰ø°Â∞èÁ®ãÂ∫èÊ∑ªÂä†Ê†áËÆ∞
    addWeixinMarker(lat, lng, title, id) {
      this.markers.push({
        id: id,
        latitude: lat,
        longitude: lng,
        title: title,
        // iconPath: '/static/location.png' // ÈúÄË¶ÅÂáÜÂ§áÂõæÊ†á
      });
    },
    
    // H5Ê∑ªÂä†Ê†áËÆ∞
    addH5Marker(lng, lat, title) {
      if (!this.map) return;
      
      try {
        const marker = new AMap.Marker({
          position: [lng, lat],
          title: title
        });
        this.map.add(marker);
      } catch (error) {
        console.warn('H5Ê∑ªÂä†Ê†áËÆ∞Â§±Ë¥•:', error);
      }
    },
    
    // Ëé∑ÂèñÂú∞ÂùÄÂêçÁß∞
    async getAddressName(lng, lat) {
      try {
        const url = `https://restapi.amap.com/v3/geocode/regeo?key=${AMAP_WEB_KEY}&location=${lng},${lat}&output=JSON`;
        const response = await uni.request({ url });
        const data = response[1].data;
        
        if (data.status === '1' && data.regeocode) {
          this.startPoint = data.regeocode.formatted_address;
        }
      } catch (error) {
        console.warn('Ëé∑ÂèñÂú∞ÂùÄÂ§±Ë¥•:', error);
      }
    },
    
    // ‰ΩøÁî®ÂΩìÂâç‰ΩçÁΩÆ
    useCurrentLocation() {
      if (this.currentLocation) {
        this.getAddressName(this.currentLocation.longitude, this.currentLocation.latitude);
      }
    },
    
    // Ê∏ÖÈô§ÁªàÁÇπ
    clearEndPoint() {
      this.endPoint = '';
    },
    
    // ËÆæÁΩÆË∑ØÁ∫øÁ±ªÂûã
    setRouteType(type) {
      this.routeType = type;
    },
    
    // ËæìÂÖ•Ê°ÜÁÑ¶ÁÇπ‰∫ã‰ª∂
    onStartFocus() {
      // ÂèØ‰ª•Âú®ËøôÈáåÂÆûÁé∞Âú∞ÂùÄÊêúÁ¥¢
    },
    
    onEndFocus() {
      // ÂèØ‰ª•Âú®ËøôÈáåÂÆûÁé∞Âú∞ÂùÄÊêúÁ¥¢
    },
    
    // Ë∑ØÁ∫øËßÑÂàí - Â§öÂπ≥Âè∞ÈÄöÁî®
    async planRoute() {
      if (!this.canNavigate) {
        uni.showToast({
          title: 'ËØ∑ËæìÂÖ•Ëµ∑ÁÇπÂíåÁªàÁÇπ',
          icon: 'none'
        });
        return;
      }
      
      this.loading = true;
      
      try {
        const routeData = await this.getRouteData();
        
        if (routeData) {
          this.currentRoute = this.formatRouteData(routeData);
          this.drawRoute(routeData);
          uni.showToast({
            title: 'Ë∑ØÁ∫øËßÑÂàíÊàêÂäü',
            icon: 'success'
          });
        } else {
          uni.showToast({
            title: 'Ë∑ØÁ∫øËßÑÂàíÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•Âú∞ÂùÄ',
            icon: 'none'
          });
        }
      } catch (error) {
        console.error('Ë∑ØÁ∫øËßÑÂàíÈîôËØØ:', error);
        uni.showToast({
          title: 'Ë∑ØÁ∫øËßÑÂàíÂ§±Ë¥•',
          icon: 'none'
        });
      } finally {
        this.loading = false;
      }
    },
    
    // Ëé∑ÂèñË∑ØÁ∫øÊï∞ÊçÆ
    async getRouteData() {
      const strategy = this.getRouteStrategy();
      const url = `https://restapi.amap.com/v3/direction/driving?key=${AMAP_WEB_KEY}&origin=${encodeURIComponent(this.startPoint)}&destination=${encodeURIComponent(this.endPoint)}&strategy=${strategy}&output=JSON`;
      
      try {
        const response = await uni.request({ url });
        const data = response[1].data;
        
        if (data.status === '1' && data.route && data.route.paths && data.route.paths.length > 0) {
          return data.route.paths[0];
        }
        return null;
      } catch (error) {
        console.error('Ëé∑ÂèñË∑ØÁ∫øÊï∞ÊçÆÂ§±Ë¥•:', error);
        return null;
      }
    },
    
    // Ëé∑ÂèñË∑ØÁ∫øÁ≠ñÁï•
    getRouteStrategy() {
      switch (this.routeType) {
        case 'drive': return '0'; // ÊúÄÂø´Ë∑ØÁ∫ø
        case 'bus': return '6';   // ÂÖ¨‰∫§Á≠ñÁï•
        case 'walk': return '2';  // Ê≠•Ë°åÁ≠ñÁï•
        default: return '0';
      }
    },
    
    // Ê†ºÂºèÂåñË∑ØÁ∫øÊï∞ÊçÆ
    formatRouteData(route) {
      const steps = (route.steps || []).map(step => ({
        instruction: step.instruction.replace(/<[^>]*>/g, ''),
        distance: (step.distance / 1000).toFixed(1) + 'ÂÖ¨Èáå',
        icon: this.getStepIcon(step.instruction)
      }));
      
      return {
        distance: (route.distance / 1000).toFixed(1) + 'ÂÖ¨Èáå',
        duration: this.formatDuration(route.duration),
        steps: steps
      };
    },
    
    // Ëé∑ÂèñÊ≠•È™§ÂõæÊ†á
    getStepIcon(instruction) {
      const inst = instruction.toLowerCase();
      if (inst.includes('Â∑¶ËΩ¨')) return '‚Ü∞';
      if (inst.includes('Âè≥ËΩ¨')) return '‚Ü±';
      if (inst.includes('Áõ¥Ë°å')) return '‚Üë';
      if (inst.includes('Âà∞Ëææ')) return 'üèÅ';
      if (inst.includes('Âá∫Âèë')) return 'üö©';
      return '‚Ä¢';
    },
    
    // Ê†ºÂºèÂåñÊó∂Èïø
    formatDuration(seconds) {
      const hours = Math.floor(seconds / 3600);
      const minutes = Math.floor((seconds % 3600) / 60);
      
      if (hours > 0) {
        return `${hours}Â∞èÊó∂${minutes}ÂàÜÈíü`;
      } else {
        return `${minutes}ÂàÜÈíü`;
      }
    },
    
    // ÁªòÂà∂Ë∑ØÁ∫ø - Â§öÂπ≥Âè∞ÂÖºÂÆπ
    drawRoute(route) {
      if (!route.steps) return;
      
      // #ifdef MP-WEIXIN
      this.drawWeixinRoute(route);
      // #endif
      
      // #ifdef H5
      this.drawH5Route(route);
      // #endif
    },
    
    // ÂæÆ‰ø°Â∞èÁ®ãÂ∫èÁªòÂà∂Ë∑ØÁ∫ø
    drawWeixinRoute(route) {
      // Ê∏ÖÈô§‰πãÂâçÁöÑÊ†áËÆ∞ÂíåË∑ØÁ∫ø
      this.markers = [];
      this.polyline = [];
      
      // Ê∑ªÂä†Ëµ∑ÁÇπÁªàÁÇπÊ†áËÆ∞
      if (route.steps[0]) {
        const startLoc = route.steps[0].start_location.split(',');
        this.addWeixinMarker(parseFloat(startLoc[1]), parseFloat(startLoc[0]), 'Ëµ∑ÁÇπ', 1);
      }
      
      if (route.steps[route.steps.length - 1]) {
        const endLoc = route.steps[route.steps.length - 1].end_location.split(',');
        this.addWeixinMarker(parseFloat(endLoc[1]), parseFloat(endLoc[0]), 'ÁªàÁÇπ', 2);
      }
      
      // ÁªòÂà∂Ë∑ØÁ∫ø
      const points = [];
      route.steps.forEach(step => {
        if (step.polyline) {
          const stepPoints = step.polyline.split(';');
          stepPoints.forEach(point => {
            const [lng, lat] = point.split(',');
            points.push({
              latitude: parseFloat(lat),
              longitude: parseFloat(lng)
            });
          });
        }
      });
      
      if (points.length > 0) {
        this.polyline = [{
          points: points,
          color: '#1E90FF',
          width: 6,
          dottedLine: false
        }];
      }
    },
    
    // H5ÁªòÂà∂Ë∑ØÁ∫ø
    drawH5Route(route) {
      if (!this.map || !route.steps) return;
      
      try {
        // Ê∏ÖÈô§‰πãÂâçÁöÑË∑ØÁ∫ø
        this.map.clearMap();
        
        // Ê∑ªÂä†Ëµ∑ÁÇπÁªàÁÇπÊ†áËÆ∞
        if (route.steps[0]) {
          const startLoc = route.steps[0].start_location.split(',');
          this.addH5Marker(parseFloat(startLoc[0]), parseFloat(startLoc[1]), 'Ëµ∑ÁÇπ');
        }
        
        if (route.steps[route.steps.length - 1]) {
          const endLoc = route.steps[route.steps.length - 1].end_location.split(',');
          this.addH5Marker(parseFloat(endLoc[0]), parseFloat(endLoc[1]), 'ÁªàÁÇπ');
        }
        
        // ÁªòÂà∂Ë∑ØÁ∫ø
        const path = [];
        route.steps.forEach(step => {
          if (step.polyline) {
            const points = step.polyline.split(';');
            points.forEach(point => {
              const [lng, lat] = point.split(',');
              path.push([parseFloat(lng), parseFloat(lat)]);
            });
          }
        });
        
        if (path.length > 0) {
          const polyline = new AMap.Polyline({
            path: path,
            strokeColor: '#1E90FF',
            strokeWeight: 6,
            strokeOpacity: 0.8
          });
          this.map.add(polyline);
          this.map.setFitView();
        }
      } catch (error) {
        console.warn('H5ÁªòÂà∂Ë∑ØÁ∫øÂ§±Ë¥•:', error);
      }
    },
    
    // ÊâìÂºÄÂ§ñÈÉ®ÂØºËà™
    openExternalNavigation() {
      if (!this.canNavigate) {
        uni.showToast({
          title: 'ËØ∑ËæìÂÖ•Ëµ∑ÁÇπÂíåÁªàÁÇπ',
          icon: 'none'
        });
        return;
      }
      
      // ‰ΩøÁî®È´òÂæ∑Âú∞ÂõæURIÊñπÊ°à
      const url = `https://uri.amap.com/navigation?from=${encodeURIComponent(this.startPoint)}&to=${encodeURIComponent(this.endPoint)}&mode=car&callnative=1`;
      
      // #ifdef MP-WEIXIN
      // ÂæÆ‰ø°Â∞èÁ®ãÂ∫è‰∏≠Â§çÂà∂ÈìæÊé•Âà∞Ââ™Ë¥¥Êùø
      uni.setClipboardData({
        data: url,
        success: () => {
          uni.showToast({
            title: 'ÂØºËà™ÈìæÊé•Â∑≤Â§çÂà∂ÔºåËØ∑Á≤òË¥¥Âà∞ÊµèËßàÂô®ÊâìÂºÄ',
            icon: 'success'
          });
        }
      });
      // #endif
      
      // #ifdef H5
      window.open(url, '_blank');
      // #endif
    },
    
    // ËøîÂõû
    goBack() {
      uni.navigateBack();
    }
  }
}
</script>

